.slideBoxx{ 
    width:100%; height:394px; overflow:hidden; position:relative; /* margin-bottom:50px; */
    .hd{ height:12px; overflow:hidden; width:90px; position:absolute; right:50%; margin-right:-45px; display:inline; bottom:20px; z-index:1;}
