    .hd ul li.on{ background:#711d1b; color:#fff; }
    .bd{ position:relative; width:100%; height:100%; z-index:0;background-color: #333;transition: all .5s ease-in .2s;}
    .bd li{ zoom:1; vertical-align:middle; }
    .bd li a{ position:absolute; width:2000px; margin-left:-1000px; left:50%; vertical-align:middle;}
    .bd li img{  display:block; margin:0 auto;}
    .prev,.next{ position:absolute; left:3%; top:50%; margin-top:-28px; display:block; width:38px; height:56px; background: url(/dist/img/common/prev.jpg) -0px 0 no-repeat; filter:alpha(opacity=10);opacity:0.1;   }
    .next{ left:auto; right:3%; background-position:-96px 0px; }
    
}
.slideBoxx:hover .prev,.slideBoxx:hover .next{ filter:alpha(opacity=80);opacity:0.8; }
