/*产品列表筛选 start */
.crumbs{margin-bottom: 20px;font-size: 14px;}
.crumbs a {
    background: url("/dist/img/common/crumb_bg.gif") 100% 50% no-repeat;
    padding-right: 8px;
    margin-right: 1px
}
.category-bar a.current,.category-bar a:hover {
    text-decoration: none;
    color: #ad1415
}

.category-bar table {font-size: 14px;
    width: 100%
}

.category-bar col.key {
    width: 100px
}

.category-bar td,.category-bar th {
    border: 1px solid #dedede
}

.category-bar th {
    background-color: #fafafa;
    border-right-color: #f6f6f6;
    color: #848484;
    padding: 15px 20px;
    text-align: left;
    vertical-align: top;
    font-weight: 400
}

.category-bar .link-list,.category-bar .pre-link-list {
    padding: 8px 0
}

.category-bar .link-list {
    position: relative;
    overflow: hidden;
    -webkit-transition: height 1s ease;
    -moz-transition: height 1s ease;
    -ms-transition: height 1s ease;
    -o-transition: height 1s ease;
    transition: height 1s ease
}

.category-bar .link-list a,.category-bar .pre-link-list span {
    display: block;
    float: left;
    width: 120px;
    padding-right: 5px;
    height: 30px;
    line-height: 30px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis
}

.category-bar .pre-link-list span {
    color: #848484
}

.category-bar .link-list .more {
    position: absolute;
    right: 0;
    top: 8px;
    width: 55px
}

.category-bar .link-list .more .up,.category-bar .link-list .more.on .down {
    display: none
}

.category-bar .link-list .more .down,.category-bar .link-list .more.on .up {
    display: inline
}

.category-bar .categories,.category-bar .states,.category-bar .tags {
    padding-left: 40px
}

.category-bar .categories .link-list.folded {
    max-height: 60px
}

.category-bar .tags {
    border-top: 1px dotted #cbcbcb;
    margin-top: -1px
}

.category-bar .tags .link-list {
    margin-left: 125px;
    margin-top: -46px
}

.category-bar .tags .link-list.folded {
    max-height: 30px
}

